import React, { useEffect, lazy, Suspense } from "react";
// import Navbar from "../Sections/home/Navbar"
// import Header from "../Sections/home/Header"
// import Content from "../Sections/home/Content"
// import Footer from "../Sections/home/Footer"
import Aos from 'aos';

const Navbar = lazy(() => import("../Sections/home/Navbar"));
const Header = lazy(() => import("../Sections/home/Header"));
const Content = lazy(() => import("../Sections/home/Content"));
const Footer = lazy(() => import("../Sections/home/Footer"));

const Home = () => {

    useEffect(() => {
        Aos.init({
          duration: 1000,
        });
    },[])

    return(
        <div id="home" style={{backgroundColor: '#adadad'}} className="container-fluid overflow-hidden m-0 p-0">

            {/* <Navbar />
            <Header />
            <Content />
            <Footer /> */}
            <Suspense fallback={<div>Loading...</div>}>
                <Navbar />
                <Header />
                <Content />
                <Footer />
            </Suspense>

        </div>
    )
}

export default Home